'use client';

import { useState } from 'react';
import { useRouter } from 'next/navigation';
import { Category, CATEGORY_LABELS, Post } from '@/types/blog';
import { useAuth } from '@/context/AuthContext';
import { db } from '@/lib/firebase';
import { collection, addDoc, Timestamp } from 'firebase/firestore';
import { TiptapEditor } from '@/components/editor/TiptapEditor';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Textarea } from '@/components/ui/textarea';
import { useLocale } from 'next-intl';

export default function WritePage() {
    const router = useRouter();
    const { user } = useAuth();
    const locale = useLocale() as 'en' | 'ko';

    const [title, setTitle] = useState('');
    const [content, setContent] = useState('');
    const [category, setCategory] = useState<Category>('ISSUE');
    const [summary, setSummary] = useState('');
    const [thumbnailUrl, setThumbnailUrl] = useState('');
    const [isSubmitting, setIsSubmitting] = useState(false);

    // Todo: Implement protect route properly
    // if (!user) {
    //   return <div>Please login first</div>;
    // }

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();
        if (!title || !content) return;

        setIsSubmitting(true);
        try {
            const slug = title.toLowerCase().replace(/ /g, '-').replace(/[^\w-]+/g, '');
            const newPost: Omit<Post, 'id'> = {
                title,
                content,
                summary,
                category,
                tags: [], // Feature to be added
                authorId: user?.uid || 'anonymous',
                authorName: user?.displayName || 'Anonymous',
                createdAt: Timestamp.now(),
                updatedAt: Timestamp.now(),
                status: 'published',
                viewCount: 0,
                slug: `${slug}-${Date.now()}` // Ensure uniqueness
            };
            if (thumbnailUrl) {
                newPost.thumbnailUrl = thumbnailUrl;
            }

            await addDoc(collection(db, 'posts'), newPost);
            router.push(`/${locale}`);
        } catch (error) {
            console.error('Error adding document: ', error);
            alert('Failed to save post');
        } finally {
            setIsSubmitting(false);
        }
    };

    return (
        <div className="container mx-auto px-4 py-8 max-w-4xl">
            <h1 className="text-3xl font-bold mb-8">Write New Post</h1>
            <form onSubmit={handleSubmit} className="space-y-6">
                <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                    <div className="space-y-2">
                        <Label htmlFor="title">Title</Label>
                        <Input
                            id="title"
                            value={title}
                            onChange={(e) => setTitle(e.target.value)}
                            placeholder="Enter post title"
                            required
                        />
                    </div>
                    <div className="space-y-2">
                        <Label htmlFor="category">Category</Label>
                        <Select value={category} onValueChange={(val) => setCategory(val as Category)}>
                            <SelectTrigger>
                                <SelectValue placeholder="Select category" />
                            </SelectTrigger>
                            <SelectContent>
                                {Object.keys(CATEGORY_LABELS).map((cat) => (
                                    cat !== 'ALL' && (
                                        <SelectItem key={cat} value={cat}>
                                            {CATEGORY_LABELS[cat as Category][locale]}
                                        </SelectItem>
                                    )
                                ))}
                            </SelectContent>
                        </Select>
                    </div>
                </div>

                <div className="space-y-2">
                    <Label htmlFor="summary">Summary</Label>
                    <Textarea
                        id="summary"
                        value={summary}
                        onChange={(e) => setSummary(e.target.value)}
                        placeholder="Brief summary for list view"
                        rows={3}
                    />
                </div>

                <div className="space-y-2">
                    <Label htmlFor="thumbnail">Thumbnail URL (Optional)</Label>
                    <Input
                        id="thumbnail"
                        value={thumbnailUrl}
                        onChange={(e) => setThumbnailUrl(e.target.value)}
                        placeholder="https://..."
                    />
                </div>

                <div className="space-y-2">
                    <Label>Content</Label>
                    <TiptapEditor content={content} onChange={setContent} />
                </div>

                <div className="flex justify-end gap-4">
                    <Button type="button" variant="outline" onClick={() => router.back()}>
                        Cancel
                    </Button>
                    <Button type="submit" disabled={isSubmitting}>
                        {isSubmitting ? 'Publishing...' : 'Publish Post'}
                    </Button>
                </div>
            </form>
        </div>
    );
}
